.right-title {
  background-color: rgb(98, 126, 147);
  height: 4vh;
  margin-top: -2.3vh;
}

.comment {
  color: rgb(187, 187, 187);
}

#logo {
  height: 10vh;
  width: 10vh;
  margin-top:7vh;
  margin-left:18vw;
  margin-right: 2vw;
  border-radius: 50%;
  border-width: 0.5vh;
  border-color: white;
  border-style: solid;
}

.profile {
  margin-bottom: 7vh;
}

.profile-photo {
  height: 10vw;
  width: 10vw;
  border-radius: 50%;
  margin-left: 3vw;
}

.person-name {
  margin-top: 1vh;
  text-align: center;
}

#logo:hover {
  cursor: pointer;
}

h1 {
  color: black;
  margin-top: 11vh;
  margin-bottom: 1vh;
}

#top-bar {
  width: 100%;
  height: 10vh;
  background-color: rgb(98, 126, 147);
  display: flex;
}

#top-bar a, .profile a, .profile-bottom a {
  text-decoration: none;
}

#project a, p a, #top-bar a, .profile a, .profile-bottom a {
  color: rgb(143, 143, 143);
}

#project a:hover, p a:hover {
  color: black;
}

.profile a, .profile-bottom a {
  color: black;
  font-family: "Noto Sans",Arial,sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 21px;
  text-align: center;
  display: block;
}

.profile-bottom a {
  margin-bottom: 3vh;
}

#top-bar a {
  font-weight: bold;
}

#top-bar a:hover, .profile a:hover, .profile-bottom a:hover {
  color: rgb(91, 91, 91);
}

#middle-bar {
  width: 100%;
  height: 12vh;
  background-color: white;
  display: flex;
  margin-bottom: 2.5vh;
}

#bottom-bar {
  margin-top: 2vh;
  width: 100%;
  height: 20vh;
  background-color: rgb(98, 126, 147);
}

html { 
  background: -webkit-linear-gradient(rgba(74, 160, 225, 0.1), rgba(0, 58, 120, 0.4)), url('images/shower_background.jpg') no-repeat center center fixed; 
  background: rgb(177, 196, 211);
  background: linear-gradient(rgba(74, 160, 225, 0.1), rgba(0, 58, 120, 0.4)), url('images/shower_background.jpg') no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

#generator, .box {
  width: 45vw;
  padding: 1vw;
}

#generator {
  height: 15vh;
}

#bio, #left-bar, #right-bar, .box {
  margin-top: 1.3vh;
}

/*All types of white boxes*/
#generator, .box, #bio, #project {
  border-radius: 0.75vh;
  background-color: white;
  border-width: 1px;
  border-color: rgb(222, 222, 222);
  border-style: solid;
}

#project {
  padding-bottom: 2vh;
}

#left-bar {
  margin-left: 18vw;
}

#right-bar {
  margin-left: 1.5vw;
  width: 16vw;
  height: fit-content;
}

#content {
  display: flex;
}

button {
  color: white;
  background-color: #0079D3;
  border-radius: 4px;
  box-sizing: border-box;
  text-align: center;
  letter-spacing: 1px;
  text-decoration: none;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: .5px;
  line-height: 24px;
  text-transform: uppercase;
  margin-right: 0.5vw;
  cursor: pointer;
  border-style: none;
  outline: none;
}

.box img {
  width: 40vw;
}

iframe {
  margin-left: 1vw;
}

button:hover {
  background-color: rgb(161, 214, 254);
}

#button {
  margin-top:-10px;
}

#generator-title {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: space-between;
  margin-top:-1vh;
}

/*All types of box titles*/
#generator-title h2, #bio h2, #project h2, .box h2 {
  font-family: "IBMPlexSans",Arial,sans-serif;
  font-size: 22px;
  line-height: 22px;
  font-weight: 700;
  margin-left: 1vw;
  margin-right: 1vw;
}

#project p {
  margin-top: 2vh;
}

#text-container, #project p, .box p {
  margin-left: 1vw;
  margin-right: 1vw;
}

.box p, #text, p, li {
  font-family: "Noto Sans",Arial,sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}

#generated-post {
  display: inline-block;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  animation: 
    typing 3.5s steps(50, end);
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}